import React, { useEffect, useState } from "react";
import { Link, useLocation } from "react-router-dom";
import styled from "styled-components";
import { useActiveWeb3React } from "../../hooks";
import Switch from "../../components/Switch";

import billingIcon from "../../assets/images/billing-model.svg";
import reportsIcon from "../../assets/images/reports.svg";
import faucetIcon from "../../assets/images/faucet-icon.svg";
import documentationIcon from "../../assets/images/documentation.svg";
import codeIcon from "../../assets/images/code.svg";
import aboutIcon from "../../assets/images/question-mark.svg";

import "./sidebar.css";
import Polling from "../../components/Header/Polling";

const Sidebar = styled.div`
  position:fixed;
  top: 60px;
  bottom: 0;
  width: auto;
  left:0;
  border-right: 1px solid #ddd;
  padding: 20px;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 3;
  }
`;
const SideDiv = styled.div`
  width: fit-content;
  margin: 0 auto;
  text-align: center;
  padding-bottom: 1.5rem;
`;
const StyledLink = styled.a`
&:hover{
  .sidebar-text{
    color: #7023FF;
  }
  .default{
    display: none;
  }
  .focus{
    display:inline;
  }
}
display: flex;
color: #756F82;
margin-bottom: 30px;
text-decoration: none;
.focus{
  display:none;
}
img{
  margin-right: 10px;
}
  }
`;

export default function NavBar(prop) {
  const { account } = useActiveWeb3React();
  const [isMerchantView, setIsMerchantView] = useState(false);
  const location = useLocation();

  useEffect(() => {
    const url = location.pathname;
    if (url.includes("/customer")) {
      setIsMerchantView(false);
    } else {
      setIsMerchantView(true);
    }
  }, [location]);

  const DOCS_LINK = "https://docs.pumapay.io/home/";
  const CODE_LINK = "https://github.com/pumapayio/";
  const ABOUT_LINK = "https://pumapay.io";

  return (
    <Sidebar
      className={`sidebar-menu${prop.isMenuOpen === true ? " open" : ""}`}
    >
      <SideDiv>
        {isMerchantView ? (
          <>
            <StyledLink
              className="active"
              as={Link}
              to="/merchant/create/choose-billing-model-type"
            >
              <img className="default" src={billingIcon} alt="image1" />
              <img className="focus" src={billingIcon} alt="image1" />
              <span className="sidebar-text">Billing Model</span>
            </StyledLink>
            <StyledLink className="active" as={Link} to="/merchant/dashboard">
              <img className="default" src={reportsIcon} alt="image2" />
              <img className="focus" src={reportsIcon} alt="image2" />
              <span className="sidebar-text">Reports</span>
            </StyledLink>
          </>
        ) : (
          <StyledLink
            className="default"
            as={Link}
            to="/customer/my-subscriptions"
          >
            <img className="default" src={billingIcon} alt="image2" />
            <img className="focus" src={billingIcon} alt="image2" />
            <span className="sidebar-text">My Subscriptions</span>
          </StyledLink>
        )}

        <StyledLink className="active" href={DOCS_LINK} target="_blank">
          <img className="default" src={documentationIcon} alt="image3" />
          <img className="focus" src={documentationIcon} alt="image3" />
          <span className="sidebar-text">Documentation</span>
        </StyledLink>
        <StyledLink className="active" href={CODE_LINK} target="_blank">
          <img className="default" src={codeIcon} alt="image3" />
          <img className="focus" src={codeIcon} alt="image3" />
          <span className="sidebar-text">Code</span>
        </StyledLink>
        <StyledLink className="active" href={ABOUT_LINK} target="_blank">
          <img className="default" src={aboutIcon} alt="image3" />
          <img className="focus" src={aboutIcon} alt="image3" />
          <span className="sidebar-text">About</span>
        </StyledLink>

        <hr />
        <StyledLink
          className="active"
          as={Link}
          to={`${isMerchantView ? "/merchant" : "/customer"}/faucet`}
        >
          <img
            className="default"
            style={{ width: "1.3rem" }}
            src={faucetIcon}
            alt="image2"
          />
          <img
            className="focus"
            style={{ width: "1.3rem" }}
            src={faucetIcon}
            alt="image2"
          />
          <span className="sidebar-text">Faucet</span>
        </StyledLink>
      </SideDiv>
      {account && (
        <SideDiv>
          <span
            className={`switch-view-text ${
              prop.isMenuOpen === true ? "open" : ""
            }`}
          >
            Turn {isMerchantView ? "Off" : "On"} <br />
            Merchant View
          </span>
          <Switch toggle={isMerchantView} />
          <Polling />
        </SideDiv>
      )}
    </Sidebar>
  );
}
