import { configureStore } from "@reduxjs/toolkit";
import { save, load } from "redux-localstorage-simple";

import application from "./application/reducer";
import { updateVersion } from "./global/actions";
import user from "./user/reducer";
import multicall from "./multicall/reducer";
import transactions from "./transactions/reducer";
import supported_tokens from "./supported-tokens/reducer";
import my_subscriptions from "./my-subscriptions/reducer";
import my_billing_models from "./my-billing-models/reducer";
import pull_payment_txs from "./pull-payment-txs/reducer";
import faucet_tokens from "./faucet-tokens/reducer";

const PERSISTED_KEYS: string[] = [
  "user",
  "transactions",
  "lists",
  "bm_subscriptions",
  "my_subscriptions",
  "my_billing_models",
  "pull_payment_txs",
  "supported_tokens",
  "faucet_tokens",
];

const store = configureStore({
  reducer: {
    application,
    user,
    multicall,
    transactions,
    my_subscriptions,
    my_billing_models,
    pull_payment_txs,
    supported_tokens,
    faucet_tokens,
  },
  middleware: [save({ states: PERSISTED_KEYS })],
  preloadedState: load({ states: PERSISTED_KEYS }),
});

store.dispatch(updateVersion());

export default store;

export type AppState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;
