export const isDynamicBM = (billingModelType: string): boolean => {
  return (
    billingModelType === String(BillingModelTypes.SingleDynamic) ||
    billingModelType === String(BillingModelTypes.RecurringDynamic)
  );
};

export const isAnySingleBM = (billingModelType: string): boolean => {
  return (
    billingModelType === String(BillingModelTypes.SingleDynamic) ||
    billingModelType === String(BillingModelTypes.Single)
  );
};

export const isAnyRecurringBM = (billingModelType: string): boolean => {
  return (
    isNormalRecurring(billingModelType) ||
    isDynamicRecurring(billingModelType) ||
    isRecurringPaidTrial(billingModelType) ||
    isRecurringFreeTrial(billingModelType)
  );
};

export const isRecurringWithTrial = (billingModelType: string): boolean => {
  return (
    isRecurringPaidTrial(billingModelType) ||
    isRecurringFreeTrial(billingModelType)
  );
};

export const isNormalRecurring = (billingModelType: string): boolean => {
  return billingModelType === String(BillingModelTypes.Recurring);
};

export const isDynamicRecurring = (billingModelType: string): boolean => {
  return billingModelType === String(BillingModelTypes.RecurringDynamic);
};

export const isRecurringPaidTrial = (billingModelType: string): boolean => {
  return billingModelType === String(BillingModelTypes.RecurringPaidTrial);
};

export const isRecurringFreeTrial = (billingModelType: string): boolean => {
  return billingModelType === String(BillingModelTypes.RecurringFreeTrial);
};

export const toTokenDecimals = (
  amount: string,
  decimals: string
): BigNumber => {
  return BigNumber.from(
    bignToString((Number(amount) * Number("10") ** Number(decimals)).toString())
  );
};

export const fromTokenDecimals = (amount: string, decimals: string): Number => {
  return amount && decimals
    ? Number(amount) / Number("10") ** Number(decimals)
    : 0;
};

export const bignToString = (n): string => {
  var data = String(n).split(/[eE]/);
  if (data.length == 1) return data[0];

  var z = "",
    sign = n < 0 ? "-" : "",
    str = data[0].replace(".", ""),
    mag = Number(data[1]) + 1;

  if (mag < 0) {
    z = sign + "0.";
    while (mag++) z += "0";
    return z + str.replace(/^\-/, "");
  }
  mag -= str.length;
  while (mag--) z += "0";
  return str + z;
};

export interface IDuration {
  label: string;
  value: number;
  periodInSeconds: number;
}

const SECONDS_IN_YEAR = 31536000;
const SECONDS_IN_QUARTER = 7776000;
const SECONDS_IN_WEEK = 604800;
const SECONDS_IN_MONTH = 2628000;
const SECONDS_IN_DAY = 86400;
const SECONDS_IN_HOUR = 3600;
const SECONDS_IN_MINUTE = 60;

export const getDuration = (seconds: number): IDuration => {
  const duration: IDuration = { label: "", value: 0, periodInSeconds: 0 };
  if (seconds >= SECONDS_IN_YEAR) {
    duration.label = "Year(s)";
    duration.periodInSeconds = SECONDS_IN_YEAR;
    duration.value = Math.floor(seconds / SECONDS_IN_YEAR);
  } else if (seconds >= SECONDS_IN_QUARTER) {
    duration.label = "Quarter(s)";
    duration.periodInSeconds = SECONDS_IN_QUARTER;
    duration.value = Math.floor(seconds / SECONDS_IN_QUARTER);
  } else if (seconds >= SECONDS_IN_MONTH) {
    duration.label = "Month(s)";
    duration.periodInSeconds = SECONDS_IN_MONTH;
    duration.value = Math.floor(seconds / SECONDS_IN_MONTH);
  } else if (seconds >= SECONDS_IN_WEEK) {
    duration.label = "Week(s)";
    duration.periodInSeconds = SECONDS_IN_WEEK;
    duration.value = Math.floor(seconds / SECONDS_IN_WEEK);
  } else if (seconds >= SECONDS_IN_DAY) {
    duration.label = "Day(s)";
    duration.periodInSeconds = SECONDS_IN_DAY;
    duration.value = Math.floor(seconds / SECONDS_IN_DAY);
  } else if (seconds >= SECONDS_IN_HOUR) {
    duration.label = "Hour(s)";
    duration.periodInSeconds = SECONDS_IN_HOUR;
    duration.value = Math.floor(seconds / SECONDS_IN_HOUR);
  } else if (seconds >= SECONDS_IN_MINUTE) {
    duration.label = "Minute(s)";
    duration.periodInSeconds = SECONDS_IN_MINUTE;
    duration.value = Math.floor(seconds / SECONDS_IN_MINUTE);
  }

  return duration;
};
