import React, { useState } from "react";
import styled from "styled-components";
import { Redirect, useHistory } from "react-router-dom";
import { Link } from "react-router-dom";

const StyledSwitchButton = styled.div`
  position: relative;
  width: 40px;
  height: 24px;
  margin: 0.5rem auto;
  input {
    width: 100%;
    opacity: 0;
    left: 0;
    height: 24px;
    position: absolute;
    z-index: 3;
    cursor: pointer;
  }
  input:checked + div {
    background: #7333ff;
  }
  input:checked + div:before {
    right: 4px;
    left: unset;
  }
`;
const Slider = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  transition: 0.4s;
  background: #ccc;
  border-radius: 34px;
  :before {
    position: absolute;
    content: "";
    height: 17px;
    width: 17px;
    left: 4px;
    bottom: 4px;
    border-radius: 34px;
    background: #fff;
    transition: 0.4s;
  }
`;
export default function Switch(props?: any) {
  const history = useHistory();
  const [switchDashboard, setSwitchDashboard] = useState(false);

  const toggleCustomerDashboard = () => {
    if (!switchDashboard) {
      setSwitchDashboard(!switchDashboard);
      history.push("/customer/my-subscriptions");
    } else {
      setSwitchDashboard(!switchDashboard);
      history.push("/merchant/dashboard");
    }
  };

  return (
    <StyledSwitchButton>
      {props.setChecked ? (
        <input
          type="checkbox"
          onChange={() => {
            props.setChecked();
          }}
          checked={props.checked}
        />
      ) : (
        <input
          type="checkbox"
          checked={props.toggle}
          onChange={() => toggleCustomerDashboard()}
        />
      )}

      <Slider></Slider>
    </StyledSwitchButton>
  );
}
