import { Route, Redirect } from "react-router-dom";
import { useIsUserOnlyMerchant } from "../state/user/hooks";

function PublicRoute({ children, isAuthenticated, ...rest }) {
  const isUserOnlyMerchant = useIsUserOnlyMerchant();

  return (
    <Route
      {...rest}
      render={({ location }) =>
        !isAuthenticated ? (
          children
        ) : (
          <>
            {isUserOnlyMerchant === null ? (
              <></>
            ) : (
              <Redirect
                to={{
                  pathname: isUserOnlyMerchant
                    ? "/merchant/dashboard"
                    : "/customer/my-subscriptions",
                  state: { from: location },
                }}
              />
            )}
          </>
        )
      }
    />
  );
}

export default PublicRoute;
