import React, { useEffect, useRef } from "react";
import styled from "styled-components";
import { useActiveWeb3React } from "../../hooks";
import Jazzicon from "jazzicon";

const StyledIdenticonContainer = styled.div`
  height: 1rem;
  width: 1rem;
  border-radius: 1.125rem;
  margin: 0.5rem;
  line-height: 0.5rem;
`;

interface IdenticonProps {
  wallet?: string;
}

const defaultProps: IdenticonProps = {
  wallet: null,
};

const Identicon: React.SFC<IdenticonProps> = (props: IdenticonProps) => {
  const ref = useRef<HTMLDivElement>();

  const { account } = useActiveWeb3React();
  const address = props.wallet || account;

  useEffect(() => {
    if (address && ref.current) {
      ref.current.innerHTML = "";
      ref.current.appendChild(Jazzicon(16, parseInt(address.slice(2, 10), 16)));
    }
  }, [account, props.wallet]);

  // https://github.com/DefinitelyTyped/DefinitelyTyped/issues/30451
  return <StyledIdenticonContainer ref={ref as any} />;
};

Identicon.defaultProps = defaultProps;

export default Identicon;
