import { createWeb3ReactRoot, Web3ReactProvider } from "@web3-react/core";
import React from "react";
import ReactDOM from "react-dom";
import ReactGA from "react-ga";
import { isMobile } from "react-device-detect";
import { HashRouter } from "react-router-dom";

import { Provider } from "react-redux";
import "./i18n";
import { NetworkContextName } from "./constants";
import ThemeProvider, { FixedGlobalStyle, ThemedGlobalStyleNew } from "./theme";

import store from "./state";
import ApplicationUpdater from "./state/application/updater";
import UserUpdater from "./state/user/updater";
import TransactionUpdater from "./state/transactions/updater";
import MulticallUpdater from "./state/multicall/updater";
import SupportedTokensUpdater from "./state/supported-tokens/updater";
import FaucetTokensUpdater from "./state/faucet-tokens/updater";
import MySubscriptionsUpdater from "./state/my-subscriptions/updater";
import MyBillingModelsUpdater from "./state/my-billing-models/updater";
import MyPullPaymentTxsUpdater from "./state/pull-payment-txs/updater";

import Blocklist from "./components/Blocklist";
import getLibrary from "./utils/getLibrary";

import App from "./pages/App";

const Web3ProviderNetwork = createWeb3ReactRoot(NetworkContextName);

if ("ethereum" in window) {
  (window.ethereum as any).autoRefreshOnNetworkChange = false;
}

const GOOGLE_ANALYTICS_ID: string | undefined =
  process.env.REACT_APP_GOOGLE_ANALYTICS_ID;
// TODO: Use valid google analytics ID - https://www.npmjs.com/package/react-ga
if (typeof GOOGLE_ANALYTICS_ID === "string") {
  ReactGA.initialize(GOOGLE_ANALYTICS_ID);
  ReactGA.set({
    customBrowserType: !isMobile
      ? "desktop"
      : "web3" in window || "ethereum" in window
      ? "mobileWeb3"
      : "mobileRegular",
  });
} else {
  ReactGA.initialize("test", { testMode: true, debug: true });
}

window.addEventListener("error", (error) => {
  ReactGA.exception({
    description: `${error.message} @ ${error.filename}:${error.lineno}:${error.colno}`,
    fatal: true,
  });
});

function Updaters() {
  return (
    <>
      <UserUpdater />
      <ApplicationUpdater />
      <TransactionUpdater />
      <MulticallUpdater />
      <SupportedTokensUpdater />
      <FaucetTokensUpdater />
      <MySubscriptionsUpdater />
      <MyBillingModelsUpdater />
      <MyPullPaymentTxsUpdater />
    </>
  );
}

ReactDOM.render(
  <React.StrictMode>
    <FixedGlobalStyle />
    <Web3ReactProvider getLibrary={getLibrary}>
      <Web3ProviderNetwork getLibrary={getLibrary}>
        <Blocklist>
          <Provider store={store}>
            <Updaters />
            <ThemeProvider>
              <ThemedGlobalStyleNew />
              <HashRouter>
                <App />
              </HashRouter>
            </ThemeProvider>
          </Provider>
        </Blocklist>
      </Web3ProviderNetwork>
    </Web3ReactProvider>
  </React.StrictMode>,
  document.getElementById("root")
);
