import { createAction } from "@reduxjs/toolkit";
import { ChainId } from "../../constants";
import {
  SerializedBillingModel,
  SerializedSubscriptionDetails,
} from "../../utils/blockchain-serializers";
import { BillingModelTypes } from "../../utils";

export const clearAllMyBillingModels = createAction<{ chainId: ChainId }>(
  "my_billing_models/clearAllMyBillingModels"
);

export const addMyBillingModel = createAction<{
  chainId: ChainId;
  billingModelType: BillingModelTypes;
  billingModelDetails: SerializedBillingModel;
}>("my_billing_models/addMyBillingModel");
