import React, { useState, useEffect } from "react";
import { useLocation } from "react-router";
import styled from "styled-components";
import collapse from "../../assets/images/CollapseLeft.png";
import chevron from "../../assets/images/Vector.png";

const Wrapper = styled.div`
     &.breadcrumbs.open{background: #fff;
    border-bottom: 1px solid #ddd;
    padding: 5px;
    padding-left: 15px;
    margin-top: 70px;
    width: calc(100% - 170px);
    margin-left: 230px;}
    &.breadcrumbs {
      background: #fff;
    border-bottom: 1px solid #ddd;
    padding: 5px;
    padding-left: 15px;
    margin-top: 70px;
    width: calc(100% - 170px);
    margin-left: 60px;
    }
  }
`;

const WrapUl = styled.ul`
    display: flex;
    padding: 0;
    list-style: none;
    margin: 0;
    align-items: center;
  }
`;

const WrapLi = styled.li`
    margin: 0 8px;
    color: #756F82;
  }
`;

export default function BreadCrumb(prop) {
  const [message, setMessage] = useState("");
  const location = useLocation();
  useEffect(() => {
    const urlName = location.pathname
      .split("/")
      .pop()
      .replaceAll("new", "")
      .replaceAll("-", " ")
      .replace(/\w\S*/g, function (txt) {
        return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();
      });
    setMessage(urlName);
  }, [message, location]);

  return (
    <>
      <Wrapper
        className={`breadcrumbs ${prop.isMenuOpen === true ? "open" : ""}`}
      >
        <WrapUl>
          <WrapLi>
            <img src={collapse} alt="collapse" />
          </WrapLi>
          <WrapLi>
            <img src={chevron} alt="collapse" />
          </WrapLi>
          <WrapLi>{message}</WrapLi>
        </WrapUl>
      </Wrapper>
    </>
  );
}
