import { createReducer } from "@reduxjs/toolkit";
import { addMySubscription, clearAllMySubscriptions } from "./actions";
import { SerializedSubscriptionDetails } from "../../utils/blockchain-serializers";

export interface MySubscriptionState {
  [chainId: number]: {
    [billingModelType: string]: {
      [subscriptionId: string]: SerializedSubscriptionDetails;
    };
  };
}

export const initialState: MySubscriptionState = {};

export default createReducer(initialState, (builder) =>
  builder
    .addCase(
      clearAllMySubscriptions,
      (mySubscriptions, { payload: { chainId } }) => {
        if (!mySubscriptions[chainId]) return;
        mySubscriptions[chainId] = {};
      }
    )
    .addCase(
      addMySubscription,
      (
        mySubscriptions,
        { payload: { chainId, billingModelType, subscriptionDetails } }
      ) => {
        const subscriptionsPerChain = mySubscriptions[chainId] ?? {};
        const subscriptions = subscriptionsPerChain[billingModelType] ?? {};

        subscriptionDetails.billingModelType = billingModelType;

        subscriptions[`${subscriptionDetails.id}`] = subscriptionDetails;
        subscriptionsPerChain[String(billingModelType)] = subscriptions;
        mySubscriptions[chainId] = subscriptionsPerChain;
      }
    )
);
