export function serializeBM(
  bmID: string,
  bm: BillingModel
): SerializedBillingModel {
  const subscriptionIDs = [];
  bm.subscriptionIDs.forEach((id) => {
    subscriptionIDs.push(bigNumberToStringifiedNumber(id));
  });
  return {
    id: bmID,
    payee: bm.payee,
    merchantName: bm.merchantName,
    uniqueReference: bm.uniqueReference,
    merchantUrl: bm.merchantURL,
    token: bm.settlementToken,
    totalPayments: 0,
    subscriptionIDs: subscriptionIDs,
    name: bm.name ? bm.name : null,
    amount: bm.amount ? bigNumberToStringifiedNumber(bm.amount) : null,
    frequency: bm.frequency ? bigNumberToStringifiedNumber(bm.frequency) : null,
    numberOfPayments: bm.numberOfPayments
      ? bigNumberToStringifiedNumber(bm.numberOfPayments)
      : null,
    trialPeriod: bm.trialPeriod
      ? bigNumberToStringifiedNumber(bm.trialPeriod)
      : null,
    initialAmount: bm.initialAmount
      ? bigNumberToStringifiedNumber(bm.initialAmount)
      : null,
    creationTime: bm.creationTime
      ? bigNumberToStringifiedNumber(bm.creationTime)
      : null,
    suspendTimestamp: bm.suspendTimestamp
      ? bigNumberToStringifiedNumber(bm.suspendTimestamp)
      : null,
    deactivationTimestamp: bm.deactivationTimestamp
      ? bigNumberToStringifiedNumber(bm.deactivationTimestamp)
      : null,
    trialType: bm.recurringPPType ? bm.recurringPPType : null,
  } as SerializedBillingModel;
}

