export interface PullPayment {
  paymentAmount: BigNumber;
  executionTimestamp: BigNumber;
}

export interface SerializedPullPayment {
  id: string;
  paymentAmount: string;
  executionTimestamp: string;
}

export function serializePullPayment(
  ppID: string,
  pullPayment: PullPayment
): SerializedPullPayment {
  return {
    id: ppID,
    paymentAmount: bigNumberToStringifiedNumber(pullPayment.paymentAmount),
    executionTimestamp: bigNumberToStringifiedNumber(
      pullPayment.executionTimestamp
    ),
  } as SerializedPullPayment;
}

