import { createReducer } from "@reduxjs/toolkit";
import { addMyBillingModel, clearAllMyBillingModels } from "./actions";
import { SerializedBillingModel } from "../../utils/blockchain-serializers";

export interface MyBillingModelState {
  [chainId: number]: {
    [billingModelType: string]: {
      [billingModelId: string]: SerializedBillingModel;
    };
  };
}

export const initialState: MyBillingModelState = {};

export default createReducer(initialState, (builder) =>
  builder
    .addCase(
      clearAllMyBillingModels,
      (myBillingModels, { payload: { chainId } }) => {
        if (!myBillingModels[chainId]) return;
        myBillingModels[chainId] = {};
      }
    )
    .addCase(
      addMyBillingModel,
      (
        myBillingModels,
        { payload: { chainId, billingModelType, billingModelDetails } }
      ) => {
        const billingModelsPerChain = myBillingModels[chainId] ?? {};
        const billingModels = billingModelsPerChain[billingModelType] ?? {};

        billingModelDetails.billingModelType = billingModelType;

        billingModels[`${billingModelDetails.id}`] = billingModelDetails;
        billingModelsPerChain[String(billingModelType)] = billingModels;
        myBillingModels[chainId] = billingModelsPerChain;
      }
    )
);
