import { useEffect } from "react";
import { useDispatch } from "react-redux";
import { useActiveWeb3React } from "../../hooks";
import {
  useSinglePPContract,
  useSingleDynamicPPContract,
  useRecurringPPContract,
  useDynamicRecurringPPContract,
  useFreeTrialRecurringPPContract,
  usePaidTrialRecurringPPContract,
} from "../../hooks/useContract";
import { AppDispatch } from "../index";
import { bigNumberToStringifiedNumber, BillingModelTypes } from "../../utils";
import {
  serializeBM,
  SerializedPullPayment,
  SerializedSubscriptionDetails,
  serializePullPayment,
  serializeSubscription,
} from "../../utils/blockchain-serializers";
import { addMyBillingModel, clearAllMyBillingModels } from "./actions";
import { useBlockNumber } from "../application/hooks";

export default function Updater() {
  const dispatch = useDispatch<AppDispatch>();

  const { chainId, account } = useActiveWeb3React();
  const blockNumber = useBlockNumber();

  const singlePPContract = useSinglePPContract();
  const singleDynamicPPContract = useSingleDynamicPPContract();
  const recurringPPContract = useRecurringPPContract();
  const dynamicRecurringPPContract = useDynamicRecurringPPContract();
  const freeTrialRecurringPPContract = useFreeTrialRecurringPPContract();
  const paidTrialRecurringPPContract = usePaidTrialRecurringPPContract();

  const retrieve = async (contract, bmType) => {
    if (!contract) return;
    const billingModelIds = await contract.getBillingModelIdsByAddress(account);

    for (let id of billingModelIds) {
      let totalPayments = 0;
      let bmSubscriptions: SerializedSubscriptionDetails[] = [];

      const bmDetails = await contract["getBillingModel(uint256)"](id);
      const serializedBMDetails = serializeBM(
        bigNumberToStringifiedNumber(id),
        bmDetails
      );

      if (serializedBMDetails.subscriptionIDs.length > 0) {
        for (let subscriptionId of serializedBMDetails.subscriptionIDs) {
          const subscription = await contract.getSubscription(subscriptionId);
          let serializedSubscription = serializeSubscription(
            subscriptionId,
            BillingModelTypes[bmType] === BillingModelTypes.RecurringDynamic
              ? subscription?.subscription
              : subscription,
            BillingModelTypes[bmType] === BillingModelTypes.RecurringDynamic
              ? subscription?.initialAmount
              : null,
            BillingModelTypes[bmType] === BillingModelTypes.RecurringDynamic
              ? subscription?.trialPeriod
              : null
          );

          let pullPayments: SerializedPullPayment[] = [];
          if (serializedSubscription.pullPaymentIDs.length > 0) {
            for (let pullPaymentId of serializedSubscription.pullPaymentIDs) {
              const ppDetails = await contract["getPullPayment(uint256)"](
                pullPaymentId
              );
              const serializedPPDetails = serializePullPayment(
                pullPaymentId,
                ppDetails
              );
              pullPayments.push(serializedPPDetails);
            }
          }
          serializedSubscription.pullPayments = pullPayments;

          if (!serializedSubscription.amount) {
            serializedSubscription.amount = pullPayments.length > 0 ? pullPayments[0].paymentAmount.toString() : null;
          }
          bmSubscriptions.push(serializedSubscription);
          totalPayments += serializedSubscription.pullPaymentIDs.length;
        }
      }
      serializedBMDetails.subscriptions = bmSubscriptions;
      serializedBMDetails.totalPayments = totalPayments;
      dispatch(
        addMyBillingModel({
          chainId,
          billingModelType: bmType,
          billingModelDetails: serializedBMDetails,
        })
      );
    }
  };

  useEffect(() => {
    if (!blockNumber || !chainId || !account) return null;

    let contract;
    for (let bmType in BillingModelTypes) {
      switch (String(BillingModelTypes[bmType])) {
        case String(BillingModelTypes.Single): {
          contract = singlePPContract;
          break;
        }
        case String(BillingModelTypes.SingleDynamic): {
          contract = singleDynamicPPContract;
          break;
        }
        case String(BillingModelTypes.Recurring): {
          contract = recurringPPContract;
          break;
        }
        case String(BillingModelTypes.RecurringDynamic): {
          contract = dynamicRecurringPPContract;
          break;
        }
        case String(BillingModelTypes.RecurringFreeTrial): {
          contract = freeTrialRecurringPPContract;
          break;
        }
        case String(BillingModelTypes.RecurringPaidTrial): {
          contract = paidTrialRecurringPPContract;
          break;
        }
        default: {
          console.warn(`Unsupported Billing Model Type - ${bmType}`);
          break;
        }
      }
      retrieve(contract, bmType).catch((e) =>
        console.error(
          `Failed to retrieve my subscriptions. Reason: ${e.message}`
        )
      );
    }
  }, [blockNumber, chainId, account]);

  useEffect(() => {
    if (!chainId || !account) return null;
    dispatch(clearAllMyBillingModels({ chainId }));
  }, [chainId, account]);

  return null;
}
