import { createReducer } from "@reduxjs/toolkit";
import { addFaucetToken, clearAllFaucetTokens } from "./actions";

export interface FaucetTokenDetails {
  address: string;
  decimals: number;
  name: string;
  symbol: string;
  allowance: string;
  waitTime: string;
}

export interface FaucetTokenState {
  [chainId: number]: {
    [tokenAddress: string]: FaucetTokenDetails;
  };
}

export const initialState: FaucetTokenState = {};

export default createReducer(initialState, (builder) =>
  builder
    .addCase(clearAllFaucetTokens, (faucetTokens, { payload: { chainId } }) => {
      if (!faucetTokens[chainId]) return;
      faucetTokens[chainId] = {};
    })
    .addCase(
      addFaucetToken,
      (faucetTokens, { payload: { chainId, timestamp, tokenDetails } }) => {
        if (faucetTokens[chainId]?.[tokenDetails.address]) return;
        const tokens = faucetTokens[chainId] ?? {};
        tokens[tokenDetails.address] = tokenDetails;
        faucetTokens[chainId] = tokens;
      }
    )
);
